.titulo {
  font-family: "Staatliches", cursive;
  font-size: 30px;
  text-align: center;
  font-weight: 100;
  color: white;
  padding: 15px 0px;
  margin: 0;
}

.subtitulos {
  font-family: "Staatliches", cursive;
  font-size: 20px;
  font-weight: 100;
  padding: 0px 10px;
  text-align: center;
  color: white;
}

.parrafos {
  margin: 0.5rem;
  font-family: "Work Sans", sans-serif;
  color: white;
  font-size: 1rem;
  text-decoration: none;
}

.link {
  -webkit-text-decoration: underline 1px rgba(0, 0, 0, 0);
          text-decoration: underline 1px rgba(0, 0, 0, 0);
  transition: -webkit-text-decoration-color 0.3s;
  transition: text-decoration-color 0.3s;
  transition: text-decoration-color 0.3s, -webkit-text-decoration-color 0.3s;
}

.link:hover {
  -webkit-text-decoration-color: white;
          text-decoration-color: white;
}

.div-general {
  background-color: rgb(240, 58, 51);
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 90vh;
}

.contenedor-decorado {
  font-family: "Work Sans", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  padding: 10px;
  margin: 20px;
  background-color: #0062ae;
  color: white;
  border-radius: 10px;
  border: none;
  box-shadow: #0486e9 -4px 4px;
}

.boton-hero:hover {
  transition: 0.5s;
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

@media (max-width: 992px) {
  .contenedor-decorado {
    margin: 20px;
  }
}
.distribucion {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.img-deco {
  height: 350px;
  margin: 80px;
}

@media (max-width: 425px) {
  .distribucion {
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .img-deco {
    height: 200px;
    margin: 20px;
  }
}
